.log-in-background {

    /* background-image: linear-gradient(to bottom right, rgba(80, 180, 152,0.4), rgba(107, 243, 168, 0.4)); */
    /* background-color: white; */
    background: linear-gradient(135deg, 
      rgba(70, 133, 133, 0.3) 0%,   /* darker teal */
      rgba(80, 180, 152, 0.3) 33%,  /* teal */
      rgba(156, 219, 166, 0.3) 66%, /* green */
      rgba(222, 249, 196, 0.3) 100% /* light green */
    );
    background-size: cover;
    transform: translate(0px, -32.5px);
    height: 750px;
}

.log-in-container {

    font-size: 25px;
    transform: translate(0px, 100px);

}

.loginBtn{
  margin-left: 10px;
  background-color: #08acaf; 
  color: white; /* Blue text */
  padding: 8px 14px;
  border-radius: 15px; /* Slightly rounded corners */
  font-size: 16px;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.3s ease;
  border: 2px solid #08acaf;
}

.loginBtn:hover {
  background-color: white; /* Blue background on hover */
  color: #08acaf; /* White text on hover */
  border: 2px solid #08acaf;
}

h1{
  color:white;
  text-shadow: #50B498 1px 0 10px;
}